// "use client";
import { Login } from "../_lib/apiService";
import AccountForm from "./AccountForm";
import { useReservation } from "./ReservationContext";

function Account() {
  const { user } = useReservation();
  if (user.firstName)
    return (
      <div>
        <div className="flex items-center justify-center py-12">
          <div className="bg-primary-800 flex flex-col py-12 rounded-lg px-8">
            <h2 className="text-xl text-accent-500 tracking-widest mb-8">
              {user.firstName}YOUR ACCOUNT
            </h2>
            <AccountForm />
          </div>
        </div>
      </div>
    );
  else return <Login />;
}

export default Account;
